.salesContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.saleTitle {
  cursor: pointer;
}

.saleHeader {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  align-items: center;
  background-color: brown;
  border-radius: 5px;
  height: 60px;
  position: sticky;
  top: 0;
  width: 100%;
}

/* Sales Home Display */

.salesDisplayContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  background-color: rgba(69, 68, 68, 0.632);
  margin-top: 10px;
  padding: 0.5rem;
  width: 100%;
}

/* Sales Card */

.teacherCardContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0.5rem;
}

.teacherCard {
  display: flex;
  flex-direction: row;
  flex: 0 0 calc(33.33% - 1rem); /* Adjust width and margin as per your preference */
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease; /* Add transition for smooth effect */
  cursor: pointer;
}

.teacherCard img {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.teacherCard:hover {
  transform: scale(0.8); /* Scale down the card slightly on hover */
}

.teacherCardImg {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(2, 92, 165, 0.3);
  padding: 0.5rem;
  border-radius: 5px;
}

.teacherCardName {
  padding: 0.5rem;
}

.teacherCardName p {
  font-family: "Trirong", serif;
  text-shadow: 3px 3px 3px #ababab;
}

.teacherCard img {
  width: 200px; /* Adjust according to your preference */
  height: 200px; /* Adjust according to your preference */
  border-radius: 50%; /* To make the image circular */
  object-fit: cover; /* Ensure the image fills the circle */
  margin-bottom: 8px;
}

.teacherCard h3 {
  margin: 0;
  font-size: 18px;
  color: #333;
}

/* Sales Details */

.itemDetailsNames {
  padding: 1rem;
}

.itemDetailsNames p {
  font-size: 20px;
}

.btnIncrement {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.3rem;
  margin-top: 1rem;
}

.btnIncrement button {
  padding: 0.3rem;
  width: 50px;
  border-style: none;
  border-radius: 3px;
}

.itemNumber {
  margin-top: 1rem;
  font-size: 1.8rem;
}

.detailsContainer {
  display: flex;
  flex-direction: column;
  background-color: aliceblue;
  color: black;
  padding: 0.5rem;
}

.detailsInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: white;
  padding: 0.5rem;
}

.detailsImg {
  background-color: rgb(48, 48, 48);
  padding: 0.5rem;
  border-radius: 5px;
}

.detailsImg img {
  height: 300px;
  width: 300px;
  object-fit: cover;
}

.detailsInfoData {
  display: flex;
  flex-direction: column; /* Ensure column direction to stack items vertically */
  background-color: rgb(48, 48, 48);
  width: 100%;
  margin-left: 1rem;
  padding: 0.5rem;
  height: 320px;
  border-radius: 5px;
  overflow-y: auto; /* Enable vertical scrolling */
}

.detailsInfoData > div {
  flex: 1; /* Ensure the content takes up remaining space */
}

.selectCartContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 1rem;
}

.selectSizeContainer {
  display: flex;
  flex-direction: row;
}

.selectSize {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
}

.selectSize select {
  width: 200px;
  padding: 0.5rem;
  border-radius: 5px;
}

.selectQuantity {
  display: flex;
  padding: 0.5rem;
}

.selectQuantityInput {
  display: flex;
  flex-direction: column;
}

.selectQuantityInput input {
  padding: 0.5rem;
}

.btnAddToCart {
  display: flex;
  padding: 0.5rem;
}

.salesCartContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  background-color: rgb(49, 49, 49);
  color: white;
  padding: 0.5rem;
  border-radius: 5px;
}

.cartPrintContainer {
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  width: 300px;
  padding: 0.5rem;
  border-radius: 5px;
  color: black;
}

.headerSection {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.headerSection p {
  margin: -0.05rem;
}

.logo {
  width: 100px; /* Adjust the size as needed */
  margin-right: 20px;
}

.dateContainer {
  display: flex;
  flex-direction: column;
  background-color: rgb(179, 179, 179);
  width: 300px;
  padding: 0.5rem;
  color: black;
  border-radius: 5px;
}

.dateContainer input {
  padding: 1rem;
  border-radius: 5px;
}

.btnCheckOut {
  display: flex;
  width: 300px;
  padding: 0.5rem;
  gap: 1rem;
}

.selectCategoryContainer {
  display: flex;
  background-color: aqua;
  padding: 0.5rem;
}

.selectCategory {
  display: flex;
  flex-direction: column;
}

.selectCategory select {
  padding: 0.5rem;
  width: 210px;
}

.quantityTotal {
  display: flex;
  flex-direction: row;
}

.dateTimeContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 0.5rem;
  width: 100%;
}

.refundContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
  width: 100%;
}

.refundContainer .refundText {
  margin: -0.2rem 0;
}

.buyerNameContainer {
  display: flex;
  width: 100%;
  font-size: 0.8rem;
  padding-left: 0.5rem;
}

.cartIconContainer {
  position: relative;
  cursor: pointer;
}

.cartIcon {
  font-size: 28px;
}

.cartItemCount {
  position: absolute;
  top: -10px;
  right: -10px;
  background-color: red;
  color: white;
  border-radius: 50%;
  padding: 0.2rem 0.5rem;
  font-size: 12px;
  font-weight: bold;
}

@media print {
  .cartPrintContainer {
    width: 100%;
    overflow: visible;
  }

  .actionsColumn {
    display: none;
  }

  .salesCartContainer {
    padding: 0;
  }

  .btnCheckOut {
    display: none;
  }

  .headerSection {
    margin-bottom: 20px;
  }
}
