.financeReportContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: aliceblue;
  padding: 0.5rem;
  color: black;
  border-radius: 5px;
  height: fit-content;
}

.datePickerContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.datePicker {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}
