.dashboardContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  color: black;
}

/* Make sure the chart container takes up a good portion of the width */
.chart-container {
  width: 100%;
  margin: 20px auto; /* Center the chart horizontally */
  padding: 20px; /* Add padding for spacing */
  background-color: #f9f9f9; /* Light background for contrast */
  border-radius: 8px; /* Rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

/* Style the dashboard heading */
.dashboard-heading {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

/* Responsive design */
@media (max-width: 768px) {
  .chart-container {
    width: 95%;
    padding: 10px;
  }
}
