.clothesContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.clothesTopbar {
  display: flex;
  flex-direction: column;
  background-color: rgba(69, 68, 68, 0.632);
  border-radius: 5px;
  padding-left: 2rem;
  padding-bottom: 1rem;
}

.teacherFormTopbar {
  display: flex;
  flex-direction: column;
  background-color: rgba(69, 68, 68, 0.632);
  border-radius: 5px;
  padding-left: 2rem;
  padding-bottom: 1rem;
  padding-top: 1rem;
}

.clothesInputContainer {
  display: flex;
  flex-direction: column;
  background-color: aliceblue;
  color: black;
  border-radius: 5px;
  padding: 0.5rem;
}

.itemInputForm {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  padding: 0.5rem;
}

.itemInputForm input {
  padding: 0.8rem;
}

.mainSizeContainer {
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  border-radius: 5px;
}

.mainSizeContainer input {
  padding: 0.8rem;
}

.clothesSubContainer {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.colorDataForm {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.colorDataForm input {
  padding: 0.8rem;
  margin-bottom: 0.5rem;
}

.colorDataForm span {
  margin-bottom: 0.5rem;
}

.btnAddColors {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.btnSubmit {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  margin-bottom: 1rem;
}

.Table td,
th {
  border: none !important;
}

td {
  cursor: pointer;
}

.tableHeadTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.tableName {
  display: flex;
  flex-direction: column;
  background-color: aqua;
}

.Table:first-child {
  border-radius: 0.7rem !important;
}
.status {
  padding: 8px;
  border-radius: 9px;
}

.Details {
  color: #00b5ff !important;
}

.mainCategoryContainer {
  display: flex;
  background-color: #ffffff;
  width: 100%;
  margin-bottom: 1rem;
  border-radius: 5px;
}

.categorySubContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 100%;
  gap: 1rem;
  padding: 0.5rem;
}

.categorySubContainer input {
  padding: 0.8rem;
}

.clothesDetailMainContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.clothDetailsBtn {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.clothDetailDisplay {
  display: flex;
  flex-direction: column;
  background-color: rgb(102, 102, 102);
  margin-top: 1rem;
  border-radius: 5px;
  padding: 0.5rem;
}

.loadingContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-top: 200px;
}

.loadingText {
  margin-top: 1rem;
  animation: zoomInOut 1.5s infinite alternate; /* Animation properties */
}

.imageContainCard {
  display: flex;
  background-color: aqua;
  width: 210px;
  height: 210px;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  margin-bottom: 0.5rem;
}

.imageCard {
  width: 200px;
  height: 200px;
  overflow: hidden;
  border-radius: 5px;
  cursor: pointer;
}

.imageCard:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@keyframes zoomInOut {
  0% {
    transform: scale(1); /* Initial scale */
  }
  100% {
    transform: scale(1.1); /* Zoomed scale */
  }
}
