.clothes-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
  text-align: center;
  width: 200px;
  margin: 20px;
}

.clothes-card:hover {
  transform: scale(1.05);
}

.clothes-card-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.clothes-card-name {
  font-size: 1.5em;
  margin: 10px 0;
}

.clothes-card-price {
  font-size: 1.2em;
  color: #555;
  margin-bottom: 15px;
}
